"use client";
import React, { useEffect, useState } from "react";
import Image from "next/image";
import { useParams, useRouter } from "next/navigation";
import { useApi } from "../../context/ApiContext";
import Navbar from "../../components/Nav/page";
import Footer from "../../components/Footer/page";

export default function GoSearch() {
  const { baseUrl } = useApi();
  const params = useParams();
  const router = useRouter();

  const serviceId = params.id;
  const [selectedService, setSelectedService] = useState(null);
  const [loading, setLoading] = useState(true);
  const [loadingButton, setLoadingButton] = useState<string | null>(null);

  useEffect(() => {
    async function fetchData() {
      try {
        const res = await fetch(`${baseUrl}/api/categories`, {
          cache: "force-cache",
          next: { revalidate: 60 },
        });

        if (!res.ok) throw new Error("Failed to fetch data");

        const data = await res.json();
        const allServices = data?.data?.flatMap((cat) => cat.services || []);
        const foundService = allServices.find(
          (s) => String(s.id) === String(serviceId)
        );

        if (!foundService) {
          router.replace("/not-found");
          return;
        }

        setSelectedService(foundService);
      } catch (err) {
        console.error(err);
        router.replace("/not-found");
      } finally {
        setLoading(false);
      }
    }

    if (serviceId) fetchData();
  }, [baseUrl, serviceId]);

  const handleClick = (id: number, type: "request" | "profile") => {
    const key = `${id}-${type}`;
    setLoadingButton(key);
    const path = type === "request" ? `/send-request/${id}` : `/viewprofile/${id}`;
    setTimeout(() => {
      router.push(path);
      setLoadingButton(null);
    }, 500);
  };

  if (loading)
    return (
      <div className="flex flex-col justify-center items-center py-20 text-gray-600">
        <div className="animate-spin rounded-full h-16 w-16 border-t-4 border-green-600 border-solid mb-4"></div>
        <p className="text-lg font-medium">ÿ¨ÿßÿ±Ÿç ÿ™ÿ≠ŸÖŸäŸÑ ÿßŸÑÿ®ŸäÿßŸÜÿßÿ™...</p>
      </div>
    );

  if (!selectedService) return null;

  const workers = selectedService.workers || [];
  const heroImage = selectedService.pictureUrl || "/images/default-hero.jpg";

  return (
    <>
      <Navbar />
      <div>
        {/* Hero Section */}
        <div className="max-w-6xl mx-auto my-6 px-4 flex flex-col md:flex-row gap-6 items-center">
          <div className="w-full md:w-1/2">
            <Image
              src={heroImage}
              alt={`Banner for ${selectedService.name}`}
              width={600}
              height={400}
              className="rounded-lg object-cover w-full h-auto"
            />
          </div>
          <div className="w-full md:w-1/2">
            <h2 className="text-xl font-semibold text-green-700 mb-2">
              üîπ Top {selectedService.name} workers near you
            </h2>
            <p className="text-gray-600 text-sm mb-4">
              These workers have been highly rated for knowledge, experience, and
              customer satisfaction.
            </p>
            <hr className="border-gray-300" />
          </div>
        </div>

        {/* Workers Section */}
        <div className="max-w-5xl mx-auto bg-white rounded-lg px-4">
          {workers.length > 0 ? (
            workers.map((worker) => (
              <div
                key={worker.id}
                className="relative group flex items-center border pb-4 mb-4 border-gray-200 bg-white rounded-lg p-4 hover:shadow-md transition duration-300"
              >
                <div>
                  <Image
                    src={worker.profilePicture || "/images/default-user.png"}
                    alt={worker.name || "Worker profile"}
                    width={80}
                    height={80}
                    className="rounded-full object-cover w-20 h-20 bg-gray-200"
                  />
                </div>
                <div className="flex-1 px-4">
                  <h3 className="text-lg font-semibold text-gray-900">
                    {worker.name}
                  </h3>
                  <p className="text-gray-600 text-sm">{worker.email}</p>
                  <p className="text-gray-600 text-sm">{worker.city}</p>
                  <div className="flex items-center gap-1 text-yellow-500 text-sm">
                    {Array.from({ length: 5 }, (_, i) => (
                      <span key={i}>
                        {i < Math.round(worker.rating || 0) ? "‚òÖ" : "‚òÜ"}
                      </span>
                    ))}
                    <span className="text-gray-600 ml-2">
                      {worker.rating?.toFixed(1) || "0.0"}
                    </span>
                  </div>
                </div>

                <div className="grid grid-cols-1 gap-2 text-center">
                  {/* ÿ∑ŸÑÿ® ÿ™ÿ≥ÿπŸäÿ± */}
                  <button
                    onClick={() => handleClick(worker.id, "request")}
                    disabled={loadingButton === `${worker.id}-request`}
                    className="relative px-5 py-2 rounded-lg bg-green-600 text-white font-medium hover:bg-white hover:text-green-600 border border-transparent hover:border-green-600 transition"
                  >
                    {loadingButton === `${worker.id}-request` ? (
                      <div className="flex items-center justify-center gap-2">
                        <svg
                          className="animate-spin h-5 w-5"
                          viewBox="0 0 24 24"
                        >
                          <circle
                            className="opacity-25"
                            cx="12"
                            cy="12"
                            r="10"
                            stroke="currentColor"
                            strokeWidth="4"
                            fill="none"
                          />
                          <path
                            className="opacity-75"
                            fill="currentColor"
                            d="M4 12a8 8 0 018-8v8z"
                          />
                        </svg>
                        ÿ¨ÿßÿ±Ÿä ÿßŸÑÿ™ÿ≠ŸÖŸäŸÑ
                      </div>
                    ) : (
                        "Request Quote"
                    )}
                  </button>

                  {/* ÿπÿ±ÿ∂ ÿßŸÑŸÖÿ≤ŸäÿØ */}
                  <button
                    onClick={() => handleClick(worker.id, "profile")}
                    disabled={loadingButton === `${worker.id}-profile`}
                    className="relative px-5 py-2 rounded-lg bg-green-600 text-white font-medium hover:bg-white hover:text-green-600 border border-transparent hover:border-green-600 transition"
                  >
                    {loadingButton === `${worker.id}-profile` ? (
                      <div className="flex items-center justify-center gap-2">
                        <svg
                          className="animate-spin h-5 w-5"
                          viewBox="0 0 24 24"
                        >
                          <circle
                            className="opacity-25"
                            cx="12"
                            cy="12"
                            r="10"
                            stroke="currentColor"
                            strokeWidth="4"
                            fill="none"
                          />
                          <path
                            className="opacity-75"
                            fill="currentColor"
                            d="M4 12a8 8 0 018-8v8z"
                          />
                        </svg>
                        ÿ¨ÿßÿ±Ÿä ÿßŸÑÿ™ÿ≠ŸÖŸäŸÑ
                      </div>
                    ) : (
                      "Learn More"
                    )}
                  </button>
                </div>
              </div>
            ))
          ) : (
            <p className="text-center text-gray-600 text-sm py-10">
              ŸÑÿß ŸäŸàÿ¨ÿØ ÿπŸÖŸëÿßŸÑ ŸÖÿ™ÿßÿ≠ŸäŸÜ ŸÑŸáÿ∞Ÿá ÿßŸÑÿÆÿØŸÖÿ© ÿ≠ÿßŸÑŸäÿßŸã.
            </p>
          )}
        </div>
      </div>
      <Footer />
    </>
  );
}
